.container {
  background-color: #f8f9fa;
  display: flex;
  flex-direction: column;
  align-items: center; 
  /* padding: 20px; */
  width: 100%;
  min-height: 100vh;
  }
  
  /* Form container styling */
  .form-container {
    background: white;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    /* width: 100%; */
    text-align: center;
    margin: 20px 0;
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px; 
    align-items: center;
    justify-content: center;
  }

  .form-group{
    width: 100%;
    text-align: start;
  }

  .form-color{
    padding: 0 40px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
  }

  .form-input{
    padding: 0 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    /* gap: 10px; */
  }
  
  .form-group label {
    font-weight: 500;
    font-size: 13px;
    margin-bottom: 5px;
  }
  
  .form-group input[type="color"]
  /* .form-group input[type="text"], */
   {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .form-group input[type="file"]{
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

  .form-group input[type="text"],
  .form-group textarea {
    width: 100% !important;
  box-sizing: border-box;
  padding: 10px;
  border: 2px solid #fcc2c2;
  border-radius: 5px;
  margin-bottom: 10px;
}

.form-group input[type="text"]:focus,
  .form-group textarea:focus {
    outline: none;
  }
  
  /* Banner preview section */
  .banner-preview {
    width: 100%;
    height: 100%;
  }
  
*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Poppins";
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

